<template>
  <div class="category">
    <div class="cate-wrapper">
      <div class="cate-one clearFix" @mouseleave="childLeaveHandler">
        <div
          class="one-item"
          v-for="(item,i) in checkList" 
          :key="i"
          @mouseover="overHandler(i)"
          @mouseleave="leaveHandler"
          @click="clickItemHandler(i)"
          :class="{'hasBorder':i===currentIndex, 'allBorder':i===currentIndex&&!item.child.length}"
        >
          <CheckBox class="item-status" @click.native.stop="selectItem(i)" :checked="item.checked"></CheckBox>
          <span>{{item.name}}</span>
          <i
            v-if="item.child.length"
            :class="{iActive:i===currentIndex}"
            class="iconfont icon-xia"
          ></i>
          <!-- <i v-show="item.child.length&&i===currentIndex" class="iconfont icon-shang"></i> -->
          <!-- <i v-show="item.child.length&&i!==currentIndex" class="iconfont icon-xia"></i> -->
          <span v-if="item.child.length">(<i style="color:red">{{item.child|checkedNum}}</i>/{{item.child.length}})</span>
        </div>
        <div
          v-if="isHovering&&childList.length"
          class="child-wrapper clearFix"
          @mouseleave="childLeaveHandler"
          @mouseover="childOverHandler"
        >
          <div
            class="child-item"
            v-for="(item,index) in childList"
            :key="'ch'+index"
          >
            <CheckBox
              class="item-status"
              :checked="item.checked"
              @click.native.stop="selectChildItem(index)"
            ></CheckBox>
            <span>{{item.name}}</span>
          </div>
        </div>
      </div>
    </div>
    <div class="img-wrapper">
      <ImageEle url="https://puui.qpic.cn/vcover_vt_pic/0/dxd1v76tmu0wjuj1586243815025/350"></ImageEle>
      <ImageEle url="https://i.gtimg.cn/qqlive/img/jpgcache/files/qqvideo/hori/d/dxd1v76tmu0wjuj_big.jpg"></ImageEle>
      <ImageEle url="https://b-gold-cdn.xitu.io/v3/static/img/logo.a7995ad.svg"></ImageEle>
    </div>
    <br><hr><br>
    <ComWath :address="address"></ComWath>
  </div>
</template>

<script>
import CheckBox from "@/components/elecomp/check";
import ImageEle from "@/components/elecomp/image";
import ComWath from "@/components/vuecomp/comp-wath";

export default {
  components: {
    CheckBox,
    ImageEle,
    ComWath
  },
  data() {
    return {
      checkList: Array.apply(null, { length: 8 }).map((item, i) => ({
        name: `item${i}`,
        checked: false,
        child: this.geneContent(i)
      })),
      isHovering: false,
      currentIndex: null,
      childList: [],
      address: [
        { name: '深圳', selected: true },
        { name: '北京', selected: true },
        { name: '上海', selected: true },
        { name: '杭州', selected: true },
        { name: '广州', selected: true },
      ]
    };
  },
  filters: {
    checkedNum(list) {
      return list.filter(item => item.checked === true).length
    }
  },
  methods: {
    overHandler(index) {
      this.isHovering = true
      this.currentIndex = index
      this.childList = this.checkList[index].child
    },
    clickItemHandler() {
      console.log('00000000000000')
    },
    selectItem(index) {
      let status = !this.checkList[index].checked
      this.checkList[index].checked = status
      this.checkList[index].child.forEach(item => {
        item.checked = status
      });
    },
    selectChildItem(index) {
      let status = this.childList[index].checked
      this.childList[index].checked = !status
      this.checkList[this.currentIndex].child = this.childList
      if(this.childList.every(item => item.checked === true)) {
        this.checkList[this.currentIndex].checked = true
      } else {
        this.checkList[this.currentIndex].checked = false
      }
      console.log(this.checkList)
    },
    leaveHandler() {
    },
    geneContent(index) {
      return Array.apply(null, {length: index}).map((item,i) => ({name: index, checked: false}))
    },
    childLeaveHandler() {
      this.isHovering = false
      this.currentIndex = null
    },
    childOverHandler() {
      this.isHovering = true
    }
  }
};
</script>

